<template>
	<div class="add-update-preview">
		<el-form
			class="add-update-form"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="180px"
			>
			<el-form-item class="add-item" label="" prop="yonghuzhanghao">
				<el-input v-model="ruleForm.yonghuzhanghao" 
					placeholder="" clearable :disabled=" false  ||ro.yonghuzhanghao"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="yonghuxingming">
				<el-input v-model="ruleForm.yonghuxingming" 
					placeholder="" clearable :disabled=" false  ||ro.yonghuxingming"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="nianling">
				<el-input v-model="ruleForm.nianling" 
					placeholder="" clearable :disabled=" false  ||ro.nianling"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="tizhong">
				<el-input v-model="ruleForm.tizhong" 
					placeholder="" clearable :disabled=" false  ||ro.tizhong"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="xinlv">
				<el-input v-model="ruleForm.xinlv" 
					placeholder="" clearable :disabled=" false  ||ro.xinlv"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="xuexing">
				<el-input v-model="ruleForm.xuexing" 
					placeholder="" clearable :disabled=" false  ||ro.xuexing"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="(mmHg)" prop="xueya">
				<el-input v-model="ruleForm.xueya" 
					placeholder="(mmHg)" clearable :disabled=" false  ||ro.xueya"></el-input>
			</el-form-item>
			<el-form-item class="add-item"  label="" prop="xueyaqingkuang">
				<el-select v-model="ruleForm.xueyaqingkuang" placeholder="" :disabled=" false  ||ro.xueyaqingkuang" >
					<el-option
						v-for="(item,index) in xueyaqingkuangOptions"
						:key="index"
						:label="item"
						:value="item">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="manxingbing">
				<el-input v-model="ruleForm.manxingbing" 
					placeholder="" clearable :disabled=" false  ||ro.manxingbing"></el-input>
			</el-form-item>
			<el-form-item class="add-item"  label="" prop="tufajibing">
				<el-select v-model="ruleForm.tufajibing" placeholder="" :disabled=" false  ||ro.tufajibing" >
					<el-option
						v-for="(item,index) in tufajibingOptions"
						:key="index"
						:label="item"
						:value="item">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="dengjishijian">
				<el-date-picker
					:disabled=" false  ||ro.dengjishijian"
					value-format="yyyy-MM-dd HH:mm:ss"
					v-model="ruleForm.dengjishijian" 
					type="datetime"
					placeholder="">
				</el-date-picker>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="guominshi">
				<el-input
					type="textarea"
					:rows="8"
					placeholder=""
					v-model="ruleForm.guominshi">
					</el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="jiwangbingshi">
				<el-input
					type="textarea"
					:rows="8"
					placeholder=""
					v-model="ruleForm.jiwangbingshi">
					</el-input>
			</el-form-item>

			<el-form-item class="add-btn-item">
				<el-button class="submitBtn"  type="primary" @click="onSubmit">
					<span class="icon iconfont "></span>
					<span class="text"></span>
				</el-button>
				<el-button class="closeBtn" @click="back()">
					<span class="icon iconfont "></span>
					<span class="text"></span>
				</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				id: '',
				baseUrl: '',
				ro:{
					yonghuzhanghao : false,
					yonghuxingming : false,
					nianling : false,
					tizhong : false,
					xinlv : false,
					xuexing : false,
					xueya : false,
					xueyaqingkuang : false,
					manxingbing : false,
					guominshi : false,
					jiwangbingshi : false,
					tufajibing : false,
					dengjishijian : false,
				},
				type: '',
				userTableName: localStorage.getItem('UserTableName'),
				ruleForm: {
					yonghuzhanghao: '',
					yonghuxingming: '',
					nianling: '',
					tizhong: '',
					xinlv: '',
					xuexing: '',
					xueya: '',
					xueyaqingkuang: '',
					manxingbing: '',
					guominshi: '',
					jiwangbingshi: '',
					tufajibing: '',
					dengjishijian: '',
				},
				xueyaqingkuangOptions: [],
				tufajibingOptions: [],


				rules: {
					yonghuzhanghao: [
					],
					yonghuxingming: [
					],
					nianling: [
					],
					tizhong: [
					],
					xinlv: [
					],
					xuexing: [
					],
					xueya: [
					],
					xueyaqingkuang: [
					],
					manxingbing: [
					],
					guominshi: [
					],
					jiwangbingshi: [
					],
					tufajibing: [
					],
					dengjishijian: [
					],
				},
				centerType: false,
			};
		},
		computed: {



		},
		components: {
		},
		created() {
			if(this.$route.query.centerType){
				this.centerType = true
			}
			//this.bg();
			let type = this.$route.query.type ? this.$route.query.type : '';
			this.init(type);
			this.baseUrl = this.$config.baseUrl;
			this.ruleForm.dengjishijian = this.getCurDateTime()
		},
		methods: {
			getMakeZero(s) {
				return s < 10 ? '0' + s : s;
			},
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(type) {
				this.type = type;
				if(type=='cross'){
					var obj = JSON.parse(localStorage.getItem('crossObj'));
					for (var o in obj){
						if(o=='yonghuzhanghao'){
							this.ruleForm.yonghuzhanghao = obj[o];
							this.ro.yonghuzhanghao = true;
							continue;
						}
						if(o=='yonghuxingming'){
							this.ruleForm.yonghuxingming = obj[o];
							this.ro.yonghuxingming = true;
							continue;
						}
						if(o=='nianling'){
							this.ruleForm.nianling = obj[o];
							this.ro.nianling = true;
							continue;
						}
						if(o=='tizhong'){
							this.ruleForm.tizhong = obj[o];
							this.ro.tizhong = true;
							continue;
						}
						if(o=='xinlv'){
							this.ruleForm.xinlv = obj[o];
							this.ro.xinlv = true;
							continue;
						}
						if(o=='xuexing'){
							this.ruleForm.xuexing = obj[o];
							this.ro.xuexing = true;
							continue;
						}
						if(o=='xueya'){
							this.ruleForm.xueya = obj[o];
							this.ro.xueya = true;
							continue;
						}
						if(o=='xueyaqingkuang'){
							this.ruleForm.xueyaqingkuang = obj[o];
							this.ro.xueyaqingkuang = true;
							continue;
						}
						if(o=='manxingbing'){
							this.ruleForm.manxingbing = obj[o];
							this.ro.manxingbing = true;
							continue;
						}
						if(o=='guominshi'){
							this.ruleForm.guominshi = obj[o];
							this.ro.guominshi = true;
							continue;
						}
						if(o=='jiwangbingshi'){
							this.ruleForm.jiwangbingshi = obj[o];
							this.ro.jiwangbingshi = true;
							continue;
						}
						if(o=='tufajibing'){
							this.ruleForm.tufajibing = obj[o];
							this.ro.tufajibing = true;
							continue;
						}
						if(o=='dengjishijian'){
							this.ruleForm.dengjishijian = obj[o];
							this.ro.dengjishijian = true;
							continue;
						}
					}
				}else if(type=='edit'){
					this.info()
				}
				// 
				this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						var json = res.data.data;
						if((json.yonghuzhanghao!=''&&json.yonghuzhanghao) || json.yonghuzhanghao==0){
							this.ruleForm.yonghuzhanghao = json.yonghuzhanghao;
							this.ro.yonghuzhanghao = true;
						}
						if((json.yonghuxingming!=''&&json.yonghuxingming) || json.yonghuxingming==0){
							this.ruleForm.yonghuxingming = json.yonghuxingming;
							this.ro.yonghuxingming = true;
						}
						if((json.nianling!=''&&json.nianling) || json.nianling==0){
							this.ruleForm.nianling = json.nianling;
							this.ro.nianling = true;
						}
					}
				});
				this.xueyaqingkuangOptions = ",,".split(',')
				this.tufajibingOptions = ",".split(',')

				if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
					localStorage.removeItem('raffleType')
					setTimeout(() => {
						this.onSubmit()
					}, 300)
				}
			},

			// 
			// 
			info() {
				this.$http.get(`jiankangdangan/detail/${this.$route.query.id}`, {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.ruleForm = res.data.data;
					}
				});
			},
			// 
			async onSubmit() {
				if(!this.ruleForm.id) {
					delete this.ruleForm.userid
				}
				await this.$refs["ruleForm"].validate(async valid => {
					if(valid) {
						if(this.type=='cross'){
							var statusColumnName = localStorage.getItem('statusColumnName');
							var statusColumnValue = localStorage.getItem('statusColumnValue');
							if(statusColumnName && statusColumnName!='') {
								var obj = JSON.parse(localStorage.getItem('crossObj'));
								if(!statusColumnName.startsWith("[")) {
									for (var o in obj){
										if(o==statusColumnName){
											obj[o] = statusColumnValue;
										}
									}
									var table = localStorage.getItem('crossTable');
									await this.$http.post(table+'/update', obj).then(res => {});
								}
							}
						}


						await this.$http.post(`jiankangdangan/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(async res => {
							if (res.data.code == 0) {
								this.$message({
									message: '',
									type: 'success',
									duration: 1500,
									onClose: () => {
										this.$router.go(-1);
										
									}
								});
							} else {
								this.$message({
									message: res.data.msg,
									type: 'error',
									duration: 1500
								});
							}
						});
					}
				});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.$router.go(-1);
			},
		}
	};
</script>

