<script>
import 'animate.css'
import Swiper from "swiper";

	export default {
		//
		data() {
			return {
				baseUrl: '',
				newsList: [],
				shenghuofuwuRecommend: [],
				jiankangzhishiRecommend: [],





			}
		},
		created() {
			this.baseUrl = this.$config.baseUrl;
			this.getList();
		},
		mounted() {
			window.addEventListener('scroll', this.handleScroll)
			setTimeout(()=>{
				this.handleScroll()
			},100)
			
			this.swiperChanges()
		},
		beforeDestroy() {
			window.removeEventListener('scroll', this.handleScroll)
		},
		//
		methods: {
			swiperChanges() {
				setTimeout(()=>{
				},750)
			},


			handleScroll() {
				let arr = [
					{id:'about',css:'animate__'},
					{id:'system',css:'animate__'},
					{id:'animate_recommendshenghuofuwu',css:'animate__'},
					{id:'animate_recommendjiankangzhishi',css:'animate__'},
				]
			
				for (let i in arr) {
					let doc = document.getElementById(arr[i].id)
					if (doc) {
						let top = doc.offsetTop
						let win_top = window.innerHeight + window.pageYOffset
						// console.log(top,win_top)
						if (win_top > top && doc.classList.value.indexOf(arr[i].css) < 0) {
							// console.log(doc)
							doc.classList.add(arr[i].css)
						}
					}
				}
			},
			preHttp(str) {
				return str && str.substr(0,4)=='http';
			},
			preHttp2(str) {
				return str && str.split(',w').length>1;
			},
			getList() {
				let autoSortUrl = "";
				let data = {}
				autoSortUrl = "shenghuofuwu/autoSort";
				if(localStorage.getItem('frontToken')) {
					autoSortUrl = "shenghuofuwu/autoSort2";
				}
				data = {
					page: 1,
					limit: 5,
				}
				this.$http.get(autoSortUrl, {params: data}).then(res => {
					if (res.data.code == 0) {
						this.shenghuofuwuRecommend = res.data.data.list;
					}
				});
				autoSortUrl = "jiankangzhishi/autoSort";
				if(localStorage.getItem('frontToken')) {
					autoSortUrl = "jiankangzhishi/autoSort2";
				}
				data = {
					page: 1,
					limit: 5,
				}
				this.$http.get(autoSortUrl, {params: data}).then(res => {
					if (res.data.code == 0) {
						this.jiankangzhishiRecommend = res.data.data.list;
					}
				});
			
			},
			toDetail(path, item) {
				this.$router.push({path: '/index/' + path, query: {id: item.id}});
			},
			moreBtn(path) {
				this.$router.push({path: '/index/' + path});
			}
		}
	}
</script>

