<template>
	<div class="add-update-preview">
		<el-form
			class="add-update-form"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="180px"
			>
			<el-form-item class="add-item" label="/" prop="heartrateperminute">
				<el-input v-model.number="ruleForm.heartrateperminute" 
					placeholder="/" clearable :disabled=" false  ||ro.heartrateperminute"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="/mmhg" prop="bloodpressuremmhg">
				<el-input v-model="ruleForm.bloodpressuremmhg" 
					placeholder="/mmhg" clearable :disabled=" false  ||ro.bloodpressuremmhg"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="/" prop="sedentarydurationminute">
				<el-input v-model.number="ruleForm.sedentarydurationminute" 
					placeholder="/" clearable :disabled=" false  ||ro.sedentarydurationminute"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="movementsteps">
				<el-input v-model.number="ruleForm.movementsteps" 
					placeholder="" clearable :disabled=" false  ||ro.movementsteps"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="painlevel">
				<el-input v-model="ruleForm.painlevel" 
					placeholder="" clearable :disabled=" false  ||ro.painlevel"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="mobility">
				<el-input v-model="ruleForm.mobility" 
					placeholder="" clearable :disabled=" false  ||ro.mobility"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="sleepquality">
				<el-input v-model="ruleForm.sleepquality" 
					placeholder="" clearable :disabled=" false  ||ro.sleepquality"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="pressurelevel">
				<el-input v-model="ruleForm.pressurelevel" 
					placeholder="" clearable :disabled=" false  ||ro.pressurelevel"></el-input>
			</el-form-item>
			<el-form-item class="add-item"  label="" prop="healthprediction">
				<el-select v-model="ruleForm.healthprediction" placeholder="" :disabled=" false  ||ro.healthprediction" >
					<el-option
						v-for="(item,index) in healthpredictionOptions"
						:key="index"
						:label="item"
						:value="item">
					</el-option>
				</el-select>
			</el-form-item>

			<el-form-item class="add-btn-item">
				<el-button class="submitBtn"  type="primary" @click="onSubmit">
					<span class="icon iconfont "></span>
					<span class="text"></span>
				</el-button>
				<el-button class="closeBtn" @click="back()">
					<span class="icon iconfont "></span>
					<span class="text"></span>
				</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				id: '',
				baseUrl: '',
				ro:{
					userid : false,
					heartrateperminute : false,
					bloodpressuremmhg : false,
					sedentarydurationminute : false,
					movementsteps : false,
					painlevel : false,
					mobility : false,
					sleepquality : false,
					pressurelevel : false,
					healthprediction : false,
				},
				type: '',
				userTableName: localStorage.getItem('UserTableName'),
				ruleForm: {
					userid: '',
					heartrateperminute: '',
					bloodpressuremmhg: '',
					sedentarydurationminute: '',
					movementsteps: '',
					painlevel: '',
					mobility: '',
					sleepquality: '',
					pressurelevel: '',
					healthprediction: '',
				},
				healthpredictionOptions: [],


				rules: {
					userid: [
					],
					heartrateperminute: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
					bloodpressuremmhg: [
					],
					sedentarydurationminute: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
					movementsteps: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
					painlevel: [
					],
					mobility: [
					],
					sleepquality: [
					],
					pressurelevel: [
					],
					healthprediction: [
					],
				},
				centerType: false,
			};
		},
		computed: {



		},
		components: {
		},
		created() {
			if(this.$route.query.centerType){
				this.centerType = true
			}
			//this.bg();
			let type = this.$route.query.type ? this.$route.query.type : '';
			this.init(type);
			this.baseUrl = this.$config.baseUrl;
		},
		methods: {
			getMakeZero(s) {
				return s < 10 ? '0' + s : s;
			},
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(type) {
				this.type = type;
				if(type=='cross'){
					var obj = JSON.parse(localStorage.getItem('crossObj'));
					for (var o in obj){
						if(o=='userid'){
							this.ruleForm.userid = obj[o];
							this.ro.userid = true;
							continue;
						}
						if(o=='heartrateperminute'){
							this.ruleForm.heartrateperminute = obj[o];
							this.ro.heartrateperminute = true;
							continue;
						}
						if(o=='bloodpressuremmhg'){
							this.ruleForm.bloodpressuremmhg = obj[o];
							this.ro.bloodpressuremmhg = true;
							continue;
						}
						if(o=='sedentarydurationminute'){
							this.ruleForm.sedentarydurationminute = obj[o];
							this.ro.sedentarydurationminute = true;
							continue;
						}
						if(o=='movementsteps'){
							this.ruleForm.movementsteps = obj[o];
							this.ro.movementsteps = true;
							continue;
						}
						if(o=='painlevel'){
							this.ruleForm.painlevel = obj[o];
							this.ro.painlevel = true;
							continue;
						}
						if(o=='mobility'){
							this.ruleForm.mobility = obj[o];
							this.ro.mobility = true;
							continue;
						}
						if(o=='sleepquality'){
							this.ruleForm.sleepquality = obj[o];
							this.ro.sleepquality = true;
							continue;
						}
						if(o=='pressurelevel'){
							this.ruleForm.pressurelevel = obj[o];
							this.ro.pressurelevel = true;
							continue;
						}
						if(o=='healthprediction'){
							this.ruleForm.healthprediction = obj[o];
							this.ro.healthprediction = true;
							continue;
						}
					}
				}else if(type=='edit'){
					this.info()
				}
				// 
				this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						var json = res.data.data;
					}
				});
				this.healthpredictionOptions = ",".split(',')

				if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
					localStorage.removeItem('raffleType')
					setTimeout(() => {
						this.onSubmit()
					}, 300)
				}
			},

			// 
			// 
			info() {
				this.$http.get(`healthdata/detail/${this.$route.query.id}`, {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.ruleForm = res.data.data;
					}
				});
			},
			// 
			async onSubmit() {
				if(!this.ruleForm.id) {
					delete this.ruleForm.userid
				}
				await this.$refs["ruleForm"].validate(async valid => {
					if(valid) {
						if(this.type=='cross'){
							var statusColumnName = localStorage.getItem('statusColumnName');
							var statusColumnValue = localStorage.getItem('statusColumnValue');
							if(statusColumnName && statusColumnName!='') {
								var obj = JSON.parse(localStorage.getItem('crossObj'));
								if(!statusColumnName.startsWith("[")) {
									for (var o in obj){
										if(o==statusColumnName){
											obj[o] = statusColumnValue;
										}
									}
									var table = localStorage.getItem('crossTable');
									await this.$http.post(table+'/update', obj).then(res => {});
								}
							}
						}


						await this.$http.post(`healthdata/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(async res => {
							if (res.data.code == 0) {
								this.$message({
									message: '',
									type: 'success',
									duration: 1500,
									onClose: () => {
										this.$router.go(-1);
										
									}
								});
							} else {
								this.$message({
									message: res.data.msg,
									type: 'error',
									duration: 1500
								});
							}
						});
					}
				});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.$router.go(-1);
			},
		}
	};
</script>

